header {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Mtavruli';
    height: 20dvh;
    z-index: 5;

    .upper_header {
        width: 50%;
        height: 70%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        select {
            margin-right: 25px;
        }

        .logo_wrapper {
            height: 100%;
            width: fit-content;
            position: relative;

            a {
                left: 0;
                top: 0;
                position: absolute;
                width: 100%;
                height: 100%;
            }

            img {
                height: 100%;
                aspect-ratio: 1/1;
            }
        }
    }

    .nav-tabs {
        width: 45%;
        display: flex;
        justify-content: space-between;
        border: none;

        .active {
            background-color: #f1e9f7;
            border-radius: 15px 15px 0 0;
        }

        .nav-item {
            width: 33%;
            border: none;
        }

        .nav-link {
            padding: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            text-decoration: none;
            text-align: center;
        }

        .nav-link:active {
            color: #551a8b;
        }

        .nav-link:hover {
            border: none !important;
        }
    }
}

@media screen and (max-width:1000px) {
    .nav-tabs {
        width: 100% !important;
    }
    .upper_header{
        width: 100% !important;
    }
}

@media screen and (max-width: 500px) {
    .nav-link {
        padding: 10% !important;
        height: 80%;
    }
}